"use strict";

import { getResource } from "../services";

function createReviews() {
  class MenuReviews {
    constructor(id, comment, name, positionWork) {
      this.id = id;
      this.comment = comment;
      this.name = name;
      this.positionWork = positionWork;
      this.parent = document.querySelector(".reviews .awesome-selection");
    }

    renderReviews() {
      const review = document.createElement("div");

      review.classList.add("selection-item", "reviews-item");
      review.innerHTML = `
      <div class="reviews-wrapper">
        <div class="reviews-form">
            <p class="reviews-text">${this.comment}</p>
        </div>
        <div class="reviews-client">
          <img class="reviews-img" src="img/reviews/reviews-client_${this.id}.png" alt="client-img_${this.id}">
          <div class="reviews-name">
              <h1 class="block-title reviews-title">${this.name}</h1>
              <p class="description-text">${this.positionWork}</p>
          </div>
        </div>
      </div>`;

      this.parent.insertAdjacentElement("beforeend", review);
    }
  }

  getResource("./db.json").then((data) => {
    data.reviews.forEach((review) => {
      new MenuReviews(
        review.id,
        review.comment,
        review.name,
        review.positionWork
      ).renderReviews();
    });
  });
}

export default createReviews;
