"use strict";

import scrollElements from "./modules/scroll";
import handleBurger from "./modules/burger";
import createTabItems from "./modules/elements/tabItems";
import createSublist from "./modules/elements/sublist";
import createAwesomeItems from "./modules/elements/awesomeItems";
import createPricing from "./modules/elements/pricing";
import createReviews from "./modules/elements/reviews";
import handleModal from "./modules/modal";
import submitForms from "./modules/forms";

document.addEventListener("DOMContentLoaded", () => {
  scrollElements(".header");
  handleBurger(".header");

  createTabItems();
  createSublist();
  createAwesomeItems();
  createPricing();
  createReviews();

  handleModal(".modal");
  submitForms(".form", ".modal");
});
