"use strict";

function scrollElements(headerSelector) {
  const header = document.querySelector(headerSelector),
    headerLogo = header.querySelector(".header-logo"),
    navLinks = header.querySelectorAll("[data-scroll]");

  document.addEventListener("scroll", () => {
    const scrollPos = window.scrollY;
    scrollPos > 0
      ? header.classList.add("header-active")
      : header.classList.remove("header-active");
  });

  headerLogo.addEventListener("click", (e) => {
    e.preventDefault();

    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  });

  navLinks.forEach(scrollLink);

  function scrollLink(link) {
    link.addEventListener("click", function (e) {
      e.preventDefault();

      const linkData = this.getAttribute("data-scroll").substring(1),
        scrollTarget = document.getElementById(linkData),
        topOffset = header.offsetHeight,
        elementPosition = scrollTarget.getBoundingClientRect().top,
        offsetPosition = elementPosition - topOffset;

      window.scrollBy({
        top: offsetPosition,
        behavior: "smooth",
      });
    });
  }
}

export default scrollElements;
