"use strict";

import { getResource } from "../services";

function createAwesomeItems() {
  class MenuItems {
    constructor(id, title, text) {
      this.id = id;
      this.title = title;
      this.text = text;
      this.parent = document.querySelector(".awesome .awesome-selection");
    }

    renderitems() {
      const item = document.createElement("div");

      item.classList.add("selection-item");
      item.innerHTML = `
      <div class="block-roster block-awesome">
        <img src="img/awesome/awesome-item_${this.id}.png" alt="img-awesome_item_${this.id}">
      </div>
      <h1 class="description-title awesome-title">${this.title}</h1>
      <p>${this.text}</p>`;

      this.parent.insertAdjacentElement("beforeend", item);
    }
  }

  getResource("db.json").then((data) => {
    data.awesomeItems.forEach((item) => {
      new MenuItems(item.id, item.title, item.text).renderitems();
    });
  });
}

export default createAwesomeItems;
