"use strict";

function handleTabs() {
  const tabsBtn = document.querySelectorAll(".click-item"),
    tabsItem = document.querySelectorAll(".item-wrapper");

  tabsBtn.forEach(onTabClick);
  function onTabClick(item) {
    item.addEventListener("click", function (e) {
      e.preventDefault();

      const tabId = this.getAttribute("data-tab"),
        currentTab = document.querySelector(tabId);

      if (!currentTab.classList.contains("item-active")) {
        tabsBtn.forEach(function (item) {
          item.classList.remove("tab-active");
        });
        tabsItem.forEach(function (item) {
          item.classList.remove("item-active");
        });

        this.classList.add("tab-active");
        currentTab.classList.add("item-active");
      }
    });
  }
  document.querySelector(".click-item").click();
}

export default handleTabs;
