"use strict";

import { getResource } from "../services";

function createPricing() {
  class MenuPricing {
    constructor(versison, price, service, popularity, text) {
      this.versison = versison;
      this.price = price;
      this.service = service;
      this.popularity = popularity;
      this.text = text;
      this.getText();
      this.parent = document.querySelector(".pricing .awesome-selection");
    }

    renderPricing() {
      const price = document.createElement("div");

      price.classList.add("selection-pricing");
      price.innerHTML = `
      <div class="pricing-item pricing-title">
        <h2 class="title-style">${this.versison}</h2>
      </div>
      <div class="pricing-item pricing-title_sup">
        <h2 class="title-style title_sup-style">${this.price}</h2>
        <p>${this.service}</p>
      </div>
      <div class="pricing-item pricing-border_orange">
        <p>${this.popularity}</p>
      </div>
      <div class="pricing-item prcing-description">${this.text}</div>`;

      this.parent.insertAdjacentElement("beforeend", price);
    }

    getText() {
      this.text = this.text
        .map((elem) => `<p class="pricing-text">${elem}</p>`)
        .join("");
    }
  }

  getResource("./db.json").then((data) => {
    data.pricing.forEach((price) => {
      new MenuPricing(
        price.version,
        price.price,
        price.service,
        price.popularity,
        price.text
      ).renderPricing();
    });
  });
}

export default createPricing;
