"use strict";

import { getResource } from "../services";

function createSublist() {
  class MenuSublist {
    constructor(position, title, text) {
      this.position = position;
      this.title = title;
      this.text = text;
      this.parent = document.querySelector(".sublist-description");
    }
    renderSublist() {
      this.parent.insertAdjacentHTML(
        "beforeend",
        `<div class="sublist-block">
            <div class="block-roster">
                <img src="img/sublist/img-roster_${this.position}.png" alt="img-roster_${this.position}">
            </div>
            <div class="block-text">
                <h4 class="block-title">${this.title}</h4>
                <p>${this.text}</p>
            </div>
        </div>`
      );
    }
  }

  getResource("./db.json").then((data) => {
    data.sublist.forEach((price) => {
      new MenuSublist(price.position, price.title, price.text).renderSublist();
    });
  });
}

export default createSublist;
