"use strict";

import { postData } from "./services";
import { openModal } from "./modal";

function submitForms(formSelector, modalSelector) {
  const forms = document.querySelectorAll(formSelector),
    message = {
      loading: "Загрузка",
      success: "<span>Cпасибо!</span><br>Мы свяжемся с Вами как можно быстрее.",
      failure: "<span>Что-то</span> пошло не так...",
    };

  forms.forEach((form) => {
    bindPostData(form);
  });

  function bindPostData(form) {
    form.addEventListener("submit", (e) => {
      e.preventDefault();

      const statusMessage = form.querySelector("[data-modal]");
      statusMessage.textContent = message.loading;

      const formData = new FormData(form),
        json = JSON.stringify(Object.fromEntries(formData.entries()));

      postData("server.php", json)
        .then((data) => getActions(data, message.success))
        .catch((e) => getActions(e, message.failure))
        .finally(() => form.reset());

      function getActions(log, message) {
        console.log(log);
        showThanksModal(message);
        statusMessage.textContent = "Перезвонить";
      }
    });
  }

  function showThanksModal(message) {
    const modalDialog = document.querySelector(".modal-dialog");
    modalDialog.innerHTML = "";

    openModal(modalSelector);

    const modalContent = document.createElement("div");
    modalContent.classList.add("modal-content");
    modalContent.innerHTML = `
      <form action="#">
        <div data-close class="modal-close">&times;</div>
        <div class="modal-title">${message}</div>
      </form>`;

    modalDialog.append(modalContent);
  }
}

export default submitForms;
