"use strict";

import { getResource } from "../services";
import handleTabs from "../tabs";

function createTabItems() {
  class MenuItems {
    constructor(id, textPrev, text) {
      this.id = id;
      this.textPrev = textPrev;
      this.text = text;
      this.parentBtn = document.querySelector(".tabs-click");
      this.parentItem = document.querySelector(".tabs .social-inner");
    }

    renderBtn() {
      const btn = document.createElement("a");

      btn.href = "#";
      btn.classList.add("click-item");
      btn.dataset.tab = `#item-${this.id}`;
      btn.innerHTML = `
      <span class="click-name"><span>Вкладка</span> ${this.id}</span>
      `;

      this.parentBtn.insertAdjacentElement("beforeend", btn);
    }

    renderItems() {
      const item = document.createElement("div");

      item.setAttribute("id", `item-${this.id}`);
      item.classList.add("item-wrapper");
      item.innerHTML = `
      <div class="tabs-item description-margin">
        <div class="item-description">
          <h1 class="description-title">Вкладки с переходным эффектом</h1>
          <p class="description-text"><span>${this.textPrev}</span> ${this.text}</p>
          <br>
          <p>Если вам нравится данный проект, Вы всегда можете скачать его шаблон:</p>
        </div>
        <div>
            <img class="item-img" src="img/tabs/tabs-item_${this.id}.jpg" alt="item-img_${this.id}">
        </div>
      </div>
      <a download href="ExployIT PDF.pdf" class="download-orange download-margin">
          <p>Загрузить</p>
      </a>`;

      this.parentItem.insertAdjacentElement("beforeend", item);
    }
  }

  getResource("db.json")
    .then((data) => {
      data.tabItems.forEach((item) => {
        new MenuItems(item.id).renderBtn();
        new MenuItems(item.id, item.textPrev, item.text).renderItems();
      });
    })
    .then(() => handleTabs());
}

export default createTabItems;
